
import Confirm from 'vux/src/components/confirm/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { JDGetIncomingMessage, JDSigning, JDAbnormalUpload } from "@/api/getData";
import $ from 'jquery'
export default {
data() {
return {
dialogImageUrl: "",
dialogVisible: false,
IncomingMessage: {},
Items: [],
Picturelist: [],
showCheck: false,
showException: false
};
},
directives: {
TransferDom
},
components: {
Confirm,
XButton,
Tabbar,
TabbarItem
},
mounted() {
this.getIncomingMessage();
},
methods: {
async getIncomingMessage() {
let data = await JDGetIncomingMessage({
FInterID: this.$route.query.FInterID
});
this.Items = data.Items;
this.Picturelist = data.Picturelist;
this.IncomingMessage = data;
console.log(data);
}
}
};



